.zantimes-two-column {
  position: relative;
}
.zantimes-two-column:before, .zantimes-two-column:after {
  content: "";
  position: absolute;
  left: 10%;
  width: 100%;
  height: 100%;
  opacity: 0.2;
}
.zantimes-two-column:before {
  top: -40px;
  background: url("../../assets/images/svg/wave-top.svg") no-repeat;
  background-position: top;
  background-size: contain;
}
.zantimes-two-column:after {
  bottom: 0;
  background: url("../../assets/images/svg/wave-bottom.svg") no-repeat;
  background-position: bottom;
  background-size: contain;
}
.zantimes-two-column__col {
  position: relative;
  z-index: 1;
}
.zantimes-two-column__col--bg {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 350px;
  overflow: hidden;
  z-index: 1;
}
.zantimes-two-column__col--bg-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.zantimes-two-column__col--bg.contain .zantimes-two-column__col--bg-image {
  object-fit: contain;
}

body:not(.wp-admin) .zantimes-two-column {
  position: relative;
  left: 50%;
  right: 50%;
  display: flex;
  flex-wrap: wrap;
  margin-left: -50vw;
  margin-right: -50vw;
  width: 100vw;
  z-index: 1;
  overflow: hidden;
}
body:not(.wp-admin) .zantimes-two-column__col {
  flex: 0 0 100%;
  padding-right: 25px;
  padding-left: 25px;
}
body:not(.wp-admin) .zantimes-two-column__col--content {
  align-self: center;
}
body:not(.wp-admin) .zantimes-two-column__col--content .acf-innerblocks-container {
  padding-top: 60px;
  padding-bottom: 60px;
  margin: auto;
  max-width: 560px;
}
body:not(.wp-admin) .zantimes-two-column__col--content .acf-innerblocks-container > *:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 992px) {
  body:not(.wp-admin) .zantimes-two-column {
    flex-wrap: nowrap;
  }
  body:not(.wp-admin) .zantimes-two-column__col {
    flex: 0 0 50%;
    padding-right: 35px;
    padding-left: 90px;
  }
  body:not(.wp-admin) .zantimes-two-column__col--content .acf-innerblocks-container {
    padding-top: 69px;
    padding-bottom: 69px;
  }
}
@media screen and (min-width: 1199px) {
  body:not(.wp-admin) .zantimes-two-column__col--bg {
    min-height: 600px;
  }
  body:not(.wp-admin) .zantimes-two-column.is-style-wide .acf-innerblocks-container {
    margin-right: -40px;
    max-width: 504px;
  }
}
@media screen and (min-width: 1234px) {
  body:not(.wp-admin) .zantimes-two-column__col--content.left {
    padding-left: calc((100vw - 1235px) / 2);
  }
  body:not(.wp-admin) .zantimes-two-column__col--content.right {
    padding-right: calc((100vw - 1235px) / 2);
  }
  body:not(.wp-admin) .zantimes-two-column.content-ms-none .zantimes-two-column__col--content.right .acf-innerblocks-container {
    margin-left: 10px;
  }
}
@media screen and (min-width: 1350px) {
  body:not(.wp-admin) .zantimes-two-column__col--content.left .acf-innerblocks-container {
    margin-left: 0;
  }
  body:not(.wp-admin) .zantimes-two-column__col--content.right .acf-innerblocks-container {
    margin-right: 0;
  }
}
@media screen and (max-width: 991px) {
  body:not(.wp-admin) .zantimes-two-column:not(.reverse-mobile) .right .acf-innerblocks-container {
    padding-top: 30px;
  }
  body:not(.wp-admin) .zantimes-two-column.reverse-mobile > .zantimes-two-column__col:first-of-type {
    order: 2;
  }
  body:not(.wp-admin) .zantimes-two-column__col--bg {
    min-height: 390px !important;
  }
  body:not(.wp-admin) .zantimes-two-column__col--bg.contain {
    padding-top: 30px;
    min-height: 0 !important;
  }
  body:not(.wp-admin) .zantimes-two-column__col--bg.contain .zantimes-two-column__col--bg-image {
    position: relative;
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 325px;
  }
  body:not(.wp-admin) .zantimes-two-column__col--content .acf-innerblocks-container {
    max-width: 100%;
  }
}